import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/Home.vue' 
import Elements from './components/elements.vue'  
import About from './components/about.vue' 
import Login from './components/login.vue'
import Register from './components/register.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',//  mode: 'history',||  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,      
      meta: {
        title: 'Home Page', 
      }
    },
    {
      path: '/elements',
      name: 'Elements',
      component: Elements,      
      meta: {
        title: 'elements', 
      }
    },
    {
      path: '/about',
      name: 'About',
      component: About,      
      meta: {
        title: 'About', 
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,      
      meta: {
        title: 'Login', 
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,      
      meta: {
        title: 'Register', 
      }
    }
  ]
})
